"use strict"
var self_
import sectionOne from './components/sectionOne';
import sectionTwo from './components/sectionTwo';
import sectionThree from './components/sectionThree';
import sectionFour from './components/sectionFour';
import sectionFive from './components/sectionFive';
import sectionSix from './components/sectionSix';
export default {
	components:{
			  sectionOne,
			  sectionTwo,
			  sectionThree,
			  sectionFour,
			  sectionFive,
			  sectionSix,
	},
  data () {
    return {
       scrollindex:0,  //当前页面的索引值
          totalnum:6,  //总共页面数
          starty:0,  //开始的位置x
          endy:0, //结束的位置y
          critical: 100, //触发翻页的临界值
          margintop:0,  //滑动下拉距离
		  transform:'translateY(-' + 0 + '%)'
    }
  },
  methods:{
	  scrollTouchstart:function(e){
		  
	      let py = e.touches[0].pageY;
		  // console.log(py)
		  self_.starty = py
	    },
	    scrollTouchmove:function(e){
	      let py = e.touches[0].pageY;
		  // console.log(py)
	      let d = this;
		  self_.endy =py;
	      if(py-d.starty<100 && py-d.starty>-100){   
			  self_.margintop=py - d.starty
	      }
	    },
	    scrollTouchend:function(e){
			
	      let d = this;
		  // console.log(d.endy)
		  // console.log(d.starty)
		  // console.log(d.scrollindex)
		  // console.log(this.totalnum)
	      if(d.endy-d.starty >100 && d.scrollindex>0){
			  self_.scrollindex=d.scrollindex-1
			  self_.transform = 'translateY(-' + self_.scrollindex*100 + '%)'
	        
	      }else if(d.endy-d.starty <-100 && d.scrollindex<this.totalnum-1 && d.endy!==0){
			   self_.scrollindex=d.scrollindex+1
			   self_.transform = 'translateY(-' + self_.scrollindex*100 + '%)'
	      }
		  self_.starty=0
		  self_.endy=0
		  self_.margintop=0
	    },
	 
  },
  onLoad() {
  	self_=this
  }
}
